___CSS_LOADER_EXPORT___.push([module.id, `@font-face {
  font-family: "Playwrite NZ Basic";
  font-style: normal;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/playwritenzbasic/v1/YcmrsYdNS1SdgmHbGgtRtk4ljD2L-0ttuA.woff2)
    format("woff2");
}

button {
  border: none;
  background-color: #4b2e1e;
  padding: 10px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 13px;
  color: #fff;
  cursor: pointer;
  transition: 0.4s;
}

button:hover {
  background-color: #e9e4e2;
  color: #4b2e1e;
}
body {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-size: cover;

}


.nav-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding-top: 10px;
}

.header {
  top: 0;
  z-index: 3;
  position: fixed;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding:10px;
  background-color: rgba(224, 208, 199, 0.4);
}

.res-name {
  display: flex;
  justify-content: center;
  align-items: center;
}
.res-name p {
  font-size:50px;
  font-weight: bold;
  font-family: "Playwrite NZ Basic";
  color: #4b2e1e;
}


.image-overlay {
  height: 100%;

  position: relative;
  
}
.image-overlay img {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}

.image-overlay::after {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
}

.hero-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
  max-width: 700px;
  gap: 15px;
}
